import Link from "next/link";
import {
  Activity,
  ArrowUpRight,
  RadioTower,
  Signal,
  Users,
  Zap,
} from "lucide-react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import type { Tower } from "@/lib/types";
import { Badge } from "@/components/ui/badge";
import StatCard from "@/components/stat-card";
import TowerCard from "@/components/tower-card";
import TowerStatusBadge from "@/components/tower-status-badge";
import { Button } from "@/components/ui/button";
import TowerMap from "@/components/tower-map";
import { Card, CardContent } from "@/components/ui/card";

async function getTowers(): Promise<Tower[]> {
  const { mockTowers } = await import("@/lib/mock-data");
  return mockTowers;
}


export default async function Dashboard() {
  const towers = await getTowers();

  const activeTowers = towers.filter((t) => t.status === "active").length;
  const avgSignal = Math.round(
    towers.reduce((acc, t) => acc + t.signalStrength, 0) /
      (towers.length || 1)
  );
  const totalUsers = towers.reduce((acc, t) => acc + t.userLoad, 0);
  const totalEnergy = towers.reduce((acc, t) => acc + t.energyConsumption, 0).toFixed(1);

  return (
    <div className="grid grid-cols-1 lg:grid-cols-12 gap-6 h-full">
      
      {/* Left Stats Column */}
      <div className="lg:col-span-3 xl:col-span-2 flex flex-col gap-6">
        <StatCard
          title="Active Towers"
          value={activeTowers.toString()}
          icon={<RadioTower className="h-4 w-4 text-muted-foreground" />}
          description={`${Math.round((activeTowers / (towers.length || 1)) * 100)}% of total`}
        />
        <StatCard
          title="Avg. Signal"
          value={`${avgSignal} dBm`}
          icon={<Signal className="h-4 w-4 text-muted-foreground" />}
          description="Across all towers"
        />
        <StatCard
          title="Connected Users"
          value={totalUsers.toLocaleString()}
          icon={<Users className="h-4 w-4 text-muted-foreground" />}
          description="Real-time user load"
        />
        <StatCard
          title="Energy Usage"
          value={`${totalEnergy} kWh`}
          icon={<Zap className="h-4 w-4 text-muted-foreground" />}
          description="Total this month"
        />
      </div>

      {/* Center Map */}
      <div className="lg:col-span-6 xl:col-span-7 h-[calc(100vh-120px)]">
         <TowerMap towers={towers} />
      </div>

      {/* Right Tower List */}
      <div className="lg:col-span-3 xl:col-span-3">
        <Card className="h-full flex flex-col">
          <CardContent className="p-4 flex-grow">
            <h3 className="font-semibold mb-4 px-2">All Towers</h3>
            <div className="overflow-y-auto h-[calc(100vh-180px)]">
              <Table>
                <TableHeader className="sticky top-0 bg-card">
                  <TableRow>
                    <TableHead>Name</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead className="text-right"></TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {towers.map((tower) => (
                    <TableRow key={tower.id}>
                      <TableCell className="font-medium">{tower.name}</TableCell>
                      <TableCell>
                        <TowerStatusBadge status={tower.status} />
                      </TableCell>
                      <TableCell className="text-right">
                          <Button asChild variant="ghost" size="icon" className="h-8 w-8">
                            <Link href={`/dashboard/towers/${tower.id}`}>
                                <ArrowUpRight className="h-4 w-4" />
                                <span className="sr-only">Details</span>
                            </Link>
                          </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
